	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"Abstract \nThe introduction of lambdas and first class function objects forever changed the nature of C++, opening up the floodgates to functional programming. Sometimes the best library design is based on a pattern that's very familiar to a Haskell programmer but alien to a C++ programmer. The new std::future proposal for C++17 hides several functional patterns including that of a monad -- the boogeyman of generations of imperative programmers. But once seen in action, the monad becomes just one more pattern in the toolbox of a library designer. \nBio\nBartosz Milewski always tries to be where the action is. He started as a quantum physicist when supertstrings were hot, joined the software industry to build the search engine for Microsoft Windows, became an avid proponent of object-oriented programming, and wrote a book on C++. He got into concurrency and parallelism when the multicores hit the market, and now he's evangelizing functional programming as the Holy Grail of software development.","meta":{"duration":"PT1H20M59S"},"presenter":{"name":null},"source":{"id":"BFnhhPehpKw","provider":"youtube"},"slug":"c17-i-see-a-monad-in-your-future","tag":["NWCPP","Bartosz Milewski","Monad","functional programming","Haskel","futures","promise","design patterns","software development","concurrent programming","parallell programming"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/BFnhhPehpKw/default.jpg","height":90,"width":120}},"title":"C++17: I See a Monad in Your Future"}},{"node":{"description":"In this video tutorial, Dan Rosen will show you how to use Scala's capacity for monadic design to eliminate repetitive boilerplate in your code.  You'll learn to recognize places where the perpetual clutter of null checks, try-catch blocks and more can be hidden within a monad, to make your code's logic stand out clearly.\r\n\r\n** Head to http://marakana.com for more open source educational resources","meta":{"duration":"PT31M22S"},"presenter":{"name":null},"source":{"id":"Mw_Jnn_Y5iA","provider":"youtube"},"slug":"scala-monads-declutter-your-code-with-monadic-design","tag":["scala","monad","monadic","design patterns","monadic design","dan rosen","marakana","techtv","Code","Demo"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/Mw_Jnn_Y5iA/default.jpg","height":90,"width":120}},"title":"Scala Monads: Declutter Your Code With Monadic Design"}}]}},"pathContext":{"slug":"design-patterns","tag":"design patterns"}}

